import React from 'react'
import { Table, Tabs } from 'antd'
import { Item, Monster, Servant } from './Subtabs'

const TabPane = Tabs.TabPane
const tabSource = [
  {
    tab: 'Servant',
    key: 'servant',
    content: <Servant />,
  },
  {
    tab: 'Monster',
    key: 'monster',
    content: <Monster />,
  },
  {
    tab: 'Item',
    key: 'item',
    content: <Item />,
  },
]

const Completed = props => (
  <>
    <Tabs className="nft-sub-tabs" defaultActiveKey="servant" size={'large'}>
      {tabSource.map(i => (
        <TabPane tab={i.tab} key={i.key}>
          {i.content}
        </TabPane>
      ))}
    </Tabs>
  </>
)

export default Completed
